<template>
  <div id="app">
    <div class="wxpro">
      <div class="con">
        <div class="proImg" v-if="content.pictUrl">
          <img alt :src="content.pictUrl" />
        </div>
        <div class="container">
          <div class="clear">
            <div class="con_pro">
              <div class="proname" v-html="content.title"></div>
              <div class="price_box">
                <div class="price">
                  <div class="price_after" v-if="parseFloat(content.zkPrice) > 0">
                    ￥ <span class="strongF">{{ (parseFloat(content.zkPrice) - parseFloat(content.couponAmount)).toFixed(2) }}</span>
                    <span class="price_before">￥{{ parseFloat(content.zkPrice).toFixed(2) }}</span>
                  </div>
                </div>
              </div>
            </div>
            <div class="coupon" v-if="parseInt(content.couponAmount) > 0  ">
              <span>￥</span>{{ content.couponAmount }}
            </div>
          </div>
        </div>
      </div>
      <div class="fixbox" @click="showModal = true">去购买</div>
    </div>
    <div class="mask" v-if="showModal">
      <div class="copy_con">
        <div class="ewm">￥{{ tkl }}￥</div>
        <div class="btn">
          <button type="button" id="buttoncopy" ref="copybtn"
            v-clipboard:copy="'￥'+tkl+'￥'"
            v-clipboard:success="handleCopySuccess"
            v-clipboard:error="handleCopyError">{{ copyButtonText }}</button></div>
        <div class="close_btn" @click="showModal = false"></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'App',
  data () {
    return {
      tkl: '',
      content: {},
      showModal: false,
      copyButtonText: '复制'
    }
  },
  mounted () {
    var _this = this
    window.onresize = function () {
      _this.setHtmlFontSize()
    }
  },
  created () {
    this.setHtmlFontSize()
    var u = this.getUserUA()
    if (!u.isMobile) {
      window.location.href = 'https://www.baidu.com'
      return false
    }
    this.checkGoods()
  },
  methods: {
    setHtmlFontSize () {
      var docEl = document.documentElement
      var fsize = 0
      var cWidth = docEl.clientWidth
      console.log(cWidth)
      fsize = cWidth / 18.75
      console.log(fsize)
      cWidth && (docEl.style.fontSize = fsize + 'px')
    },
    getUserUA () {
      var UA = navigator.userAgent
      var isAndroid = /android|adr/gi.test(UA)
      var isIOS = /iphone|ipod|ipad/gi.test(UA) && !isAndroid
      var isBlackBerry = /BlackBerry/i.test(UA)
      var isWindowPhone = /IEMobile/i.test(UA)
      var isMobile = isAndroid || isIOS || isBlackBerry || isWindowPhone
      var isIphoneX = /iphone/gi.test(navigator.userAgent) && (screen.height === 812 && screen.width === 375)
      return {
        isIphoneX: isIphoneX,
        isAndroid: isAndroid,
        isIOS: isIOS,
        isMobile: isMobile,
        isWeixin: /MicroMessenger/gi.test(UA),
        isQQ: /QQ/gi.test(UA),
        isApp: /xiaoshijie/gi.test(UA)
      }
    },
    getUrlParam (name) {
      var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)')
      var r = window.location.search.substr(1).match(reg)
      if (r != null) return decodeURIComponent(r[2])
      return ''
    },
    checkGoods () {
      this.tkl = this.getUrlParam('tkl')
      var _this = this
      this.$http({
        method: 'get',
        url: 'https://goods.bibiku.com/goods/goods?api=retriveTaobaoShareInfo&id=' + this.tkl,
        data: {}
      }).then(function (response) {
        console.log(response)
        if (response.body.code === 200 && response.body.data !== 'null') {
          _this.content = response.body.data
          console.log(_this.content)
        }
      })
    },
    handleCopySuccess () {
      this.$toast.center('复制成功')
    },
    handleCopyError () {
      this.$toast.center('复制失败，请手动复制')
    }
  }
}
</script>



